$PHONE_MAX_WIDTH: 800px;
$LAPTOP_MAX_WIDTH: 1700px;

$ONE_COL_MAX_WIDTH: 800px;
$TWO_COL_MAX_WIDTH: 1200px;


html, body { font-size: 18px; }

@media (max-width: $PHONE_MAX_WIDTH) {
    html, body { font-size: 16px; }
}
@media (min-width: $PHONE_MAX_WIDTH) and (max-width: $LAPTOP_MAX_WIDTH) {
    html, body { font-size: 20px; }
}
@media (min-width: $LAPTOP_MAX_WIDTH) {
    html, body { font-size: 24px; }
}