@import "vars";


.small-card-header {
    &.it1 {
        grid-area: h1;
    }

    &.it2 {
        grid-area: h2;
    }

    &.it3 {
        grid-area: h3;
    }

    &.it4 {
        grid-area: h4;
    }

    &.it5 {
        grid-area: h5;
    }

    &.it6 {
        grid-area: h6;
    }

    &.it7 {
        grid-area: h7;
    }

    &.it8 {
        grid-area: h8;
    }

    &.it9 {
        grid-area: h9;
    }
}

.small-card-text {
    &.it1 {
        grid-area: t1;
    }

    &.it2 {
        grid-area: t2;
    }

    &.it3 {
        grid-area: t3;
    }

    &.it4 {
        grid-area: t4;
    }

    &.it5 {
        grid-area: t5;
    }

    &.it6 {
        grid-area: t6;
    }

    &.it7 {
        grid-area: t7;
    }

    &.it8 {
        grid-area: t8;
    }

    &.it9 {
        grid-area: t9;
    }
}

.small-cards-grid {
    display: grid;
    column-gap: 1em;
    grid-template-columns: 1fr;
    // grid-template-rows: auto;

    .small-card-header {
        align-self: start;
    }

    .small-card-text {
        margin-bottom: 2em;
        // padding: 1em;
        // border: 1px solid rgba(0,0,0,0.1);
        border-radius: 0.5em;
    }
}

@media (max-width: $ONE_COL_MAX_WIDTH) {
    .small-cards-grid {
        grid-template-columns: 1fr;
        grid-template-areas:
            "h1" "t1"
            "h2" "t2"
            "h3" "t3"
            "h4" "t4"
            "h5" "t5"
            "h6" "t6"
            "h7" "t7"
            "h8" "t8"
            "h9" "t9";
    }
}

@media (min-width: $ONE_COL_MAX_WIDTH) and (max-width: $TWO_COL_MAX_WIDTH) {
    .small-cards-grid {
        grid-template-columns: 1fr 1fr;
        grid-template-areas:
            "h1 h2"
            "t1 t2"
            "h3 h4"
            "t3 t4"
            "h5 h6"
            "t5 t6"
            "h7 h8"
            "t7 t8"
            "h9 ."
            "t9 .";
    }
}

@media (min-width: $TWO_COL_MAX_WIDTH) {
    .small-cards-grid {
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-areas:
            "h1 h2 h3"
            "t1 t2 t3"
            "h4 h5 h6"
            "t4 t5 t6"
            "h7 h8 h9"
            "t7 t8 t9";
    }
}


////////////////////////////////////////////////////////////////

.small-card {
    margin-top: 1rem;
}

.small-card.big-card {
    .small-card-title {
        font-size: 2rem;
    }

    .small-card-text,
    .small-card-text li {
        font-size: 1.25rem;
        font-weight: 300;
    }
}


.small-card-text,
.small-card-text li {
    font-size: 0.8rem;
    font-weight: 300;
}

@media (max-width: $PHONE_MAX_WIDTH) {

    .small-card-text,
    .small-card-text li {
        font-size: 1rem;
    }
}


.small-card-header {
    margin-bottom: 1em;

    .small-card-title {
        font-size: 1.25rem;
        font-weight: 500;
        line-height: 100%;

        .text-title {
            margin-right: 1em;
            line-height: 130%;
        }
    }
}

.small-card-text li {
    text-align: justify;
    line-height: 170%;
}

.small-cards-grid {
    position: relative;

    .small-card {
        justify-self: center;
        max-width: calc(min(25em, 100%));
    }
}

////////////////////////////////////////////////////////////////


.small-cards-grid {
    .small-card-header {
        .small-card-title {
            .card-header-icon-holder {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 16px;
                color: #aaa;

                svg {
                    width: 1.2em;
                }
            }

            .text-title {}
        }
    }

    .small-card-text {
        li {
            &::before {
                top: 1em;
            }

            text-align: justify;
            position: relative;
            line-height: 150%;
            margin-bottom: 1em;
            list-style: none;
            // list-style-image: url(/image/bullet-blue.svg);
            // padding: 0 0 0 0.5em;
            background-size: 8px;
            background-position-y: 8px;
        }
    }
}

